<template>
  <div :class="prefixCls" class="enfi-flat-tree scroll-view">
    <div class="enfi-flat-tree-phantom" :style="`height: ${phantomHeight}px`"></div>
    <div class="enfi-flat-tree-content" ref="content">
      <ul :class="`${prefixCls}-children`" class="enfi-flat-tree-children">
        <TreeNode
          v-for="nodeData in visibleNodes"
          :data="nodeData.node"
          :key="nodeData.nodeKey"
          :node-key="nodeData.nodeKey"
          :show-checkbox="showCheckbox"
          @on-check="handleCheck"
          @on-selected="handleSelect"
          @toggle-expand="handleExpand"
        ></TreeNode>
      </ul>
    </div>
  </div>
</template>

<script>
import throttle from 'lodash.throttle'
import Node from './TreeNode'

const prefixCls = 'ivu-tree'

export default {
  name: 'FlatTree',
  components: { TreeNode: Node },

  props: {
    data: {
      type: Array,
      default () {
        return []
      },
    },
    showCheckbox: {
      type: Boolean,
      default: false,
    },
    //  showCheckbox  checkDirectlyselect  check 
    checkDirectly: {
      type: Boolean,
      default: false,
    },
    emptyText: {
      type: String,
    },
    childrenKey: {
      type: String,
      default: 'children',
    },
    render: {
      type: Function,
    },
    itemHeight: {
      type: Number,
      default: 28,
    },
  },
  data () {
    return {
      prefixCls: prefixCls,
      stateTree: this.data,
      flatState: [],
      positionConfig: {
        startIndex: 0,
        endIndex: 0,
      },
    }
  },
  watch: {
    data: {
      deep: true,
      handler: throttle(function () {
        console.log(this)
        console.log(this.data)
        this.stateTree = this.data
        this.flatState = this.compileFlatState()
        this.rebuildTree()
        this.updateVisibleData(this.$el.scrollTop)
      }, 1000, { leading: false }),
    },
  },
  computed: {
    localeEmptyText () {
      if (typeof this.emptyText === 'undefined') {
        return this.t('i.tree.emptyText')
      } else {
        return this.emptyText
      }
    },
    visibleNodes () {
      if (!this.flatState) return []
      const visible = this.flatState
        .filter(node => node.node.toshow)
        .slice(this.positionConfig.startIndex, this.positionConfig.endIndex)
      return visible
    },
    phantomHeight () {
      return this.flatState.filter(node => node.node.toshow).length * this.itemHeight
    },
  },

  created () {
    this.flatState = this.compileFlatState()
    this.rebuildTree()
  },

  mounted () {
    this.$el.addEventListener(
      'scroll',
      throttle(() => {
        this.updateVisibleData(this.$el.scrollTop)
      }, 80),
    )
    this.updateVisibleData()
  },

  methods: {
    updateVisibleData (scrollTop = 0) {
      const visibleItemCount = Math.ceil(this.$el.clientHeight / this.itemHeight)
      const newStartIndex = Math.floor(scrollTop / this.itemHeight)
      const newEndIndex = newStartIndex + visibleItemCount + 1
      this.positionConfig.startIndex = newStartIndex
      this.positionConfig.endIndex = newEndIndex
      const contentTop = newStartIndex * this.itemHeight
      this.$refs.content.style.transform = `translate3d(0, ${contentTop}px, 0)`
    },
    compileFlatState () {
      console.log('compiling flat state')
      // so we have always a relation parent/children of each node
      let keyCounter = 0
      let childrenKey = this.childrenKey
      const flatTree = []
      function flattenChildren (node, parent) {
        node.nodeKey = keyCounter++
        node.path = []
        flatTree[node.nodeKey] = { node: node, nodeKey: node.nodeKey }
        if (typeof parent !== 'undefined') {
          flatTree[node.nodeKey].parent = parent.nodeKey
          node.parent = parent.nodeKey
          node.path = parent.path.concat([parent.nodeKey])
          flatTree[parent.nodeKey][childrenKey].push(node.nodeKey)
          node.toshow = parent.expand && parent.toshow
        } else {
          node.toshow = true
        }
        if (node[childrenKey]) {
          flatTree[node.nodeKey][childrenKey] = []
          node[childrenKey].forEach(child => flattenChildren(child, node))
        }
      }
      this.stateTree.forEach(rootNode => {
        flattenChildren(rootNode)
      })
      return flatTree
    },
    rebuildTree () {
      // only called when `data` prop changes
      console.log('rebuilding tree')
      const checkedNodes = this.getCheckedNodes()
      checkedNodes.forEach(node => {
        this.updateTreeDown(node, { checked: true })
        // propagate upwards
        const parentKey = this.flatState[node.nodeKey].parent
        if (!parentKey && parentKey !== 0) return
        const parent = this.flatState[parentKey].node
        const childHasCheckSetter = typeof node.checked !== 'undefined' && node.checked
        if (childHasCheckSetter && parent.checked !== node.checked) {
          this.updateTreeUp(node.nodeKey) // update tree upwards
        }
      })
    },

    getSelectedNodes () {
      /* public API */
      return this.flatState.filter(obj => obj.node.selected).map(obj => obj.node)
    },
    getCheckedNodes () {
      /* public API */
      return this.flatState.filter(obj => obj.node.checked).map(obj => obj.node)
    },
    getCheckedAndIndeterminateNodes () {
      /* public API */
      return this.flatState
        .filter(obj => obj.node.checked || obj.node.indeterminate)
        .map(obj => obj.node)
    },

    updateTreeUp (nodeKey) {
      console.log('updating tree up')
      const parentKey = this.flatState[nodeKey].parent
      if (typeof parentKey === 'undefined' || this.checkStrictly) return

      const node = this.flatState[nodeKey].node
      const parent = this.flatState[parentKey].node
      if (
        node.checked === parent.checked &&
        node.indeterminate === parent.indeterminate
      ) {
        return // no need to update upwards
      }

      if (node.checked === true) {
        this.$set(parent, 'checked', parent[this.childrenKey].every(node => node.checked))
        this.$set(parent, 'indeterminate', !parent.checked)
      } else {
        this.$set(parent, 'checked', false)
        this.$set(
          parent,
          'indeterminate',
          parent[this.childrenKey].some(node => node.checked || node.indeterminate),
        )
      }
      this.updateTreeUp(parentKey)
    },
    updateTreeDown (node, changes = {}) {
      if (this.checkStrictly) return

      for (let key in changes) {
        this.$set(node, key, changes[key])
      }
      if (node[this.childrenKey]) {
        node[this.childrenKey].forEach(child => {
          this.updateTreeDown(child, changes)
        })
      }
    },
    handleSelect (nodeKey) {
      const node = this.flatState[nodeKey].node
      if (!this.multiple) {
        // reset previously selected node
        const currentSelectedKey = this.flatState.findIndex(obj => obj.node.selected)
        if (currentSelectedKey >= 0 && currentSelectedKey !== nodeKey) {
          this.$set(this.flatState[currentSelectedKey].node, 'selected', false)
        }
      }
      this.$set(node, 'selected', !node.selected)

      this.$emit('on-select-change', this.getSelectedNodes(), node)
    },
    handleCheck ({ checked, nodeKey }) {
      console.log('handle check')
      const node = this.flatState[nodeKey].node
      this.$set(node, 'checked', checked)
      this.$set(node, 'indeterminate', false)

      this.updateTreeUp(nodeKey) // propagate up
      this.updateTreeDown(node, { checked, indeterminate: false }) // reset `indeterminate` when going down

      this.$emit('on-check-change', this.getCheckedNodes(), node)
    },
    handleExpand ({ expand, nodeKey }) {
      const node = this.flatState[nodeKey]
      this.$emit('on-toggle-expand', node)
    },
  },
}
</script>

<style lang="scss" scoped>
.enfi-flat-tree {
  height: 100%;
  overflow: auto;
  position: relative;

  .enfi-flat-tree-phantom {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    z-index: -1;
  }

  .enfi-flat-tree-content {
    left: 0;
    right: 0;
    top: 0;
    position: absolute;
  }
}
</style>
