import qs from 'qs'
// @ts-ignore
import genBaiduLogId from '@/services/genBaiduLogid.js'
import * as ERR_CODES from '@/constants/errCodes'
import { getFileList as ladderGetFileList } from '@/services/ladder'
import baiduReq from '@/utils/baiduReq'
import { BaiduFileItem } from '@/components/types'

class BaiduFileGetter {
  isFromLadder: boolean = true // request from server or from baidu (need cookies)
  shareUrl: string = ''
  shareCode: string = ''
  surl: string = ''
  extraUrlParams: string = ''
  BAIDUID: string = ''
  BDCLND: string = ''
  fileList: BaiduFileItem[] = []
  yunData: any = {}

  clearData () {
    this.isFromLadder = true
    this.shareUrl = ''
    this.shareCode = ''
    this.surl = ''
    this.fileList = []
    this.BDCLND = ''
  }

  setUrl (shareUrl: string) {
    let surl: string = ''
    let surlMatch = shareUrl.match(/s\/1(.*?)(\/|$|&)/)
    if (surlMatch) {
      surl = surlMatch[1]
    } else {
      surlMatch = shareUrl.match(/(wap|share)\/.*?surl=1?(.*?)(&|$)/)
      if (surlMatch) {
        surl = surlMatch[2]
      }
    }
    console.log('surlmatch:')
    console.log(surlMatch, surl)
    this.clearData()
    return Promise.resolve()
      .then((): boolean | Promise<any> => {
        if (surl) {
          this.shareUrl = `https://pan.baidu.com/s/1${surl}`
          this.shareCode = ''
          this.surl = surl
          console.log(this.surl)
          baiduReq.setDefaults({ headers: { Referer: shareUrl } })
          return BaiduFileGetter.getUrlParams()
        } else {
          return Promise.reject({
            code: ERR_CODES.GET_LINK_URL_INVALID,
            msg: '',
          })
        }
      })
      .catch((err: any) => {
        console.error('file getter init failed')
        console.error(err)
        return Promise.reject(err)
      })
  }

  getFileListFromLadder (verCode?: string, verCodeStr?: string) {
    return ladderGetFileList({
      shareUrl: this.shareUrl,
      shareCode: this.shareCode,
      verCode,
      verCodeStr,
      uid: window.enfiUid,
      token: window.enfiToken,
    })
      .then(
        (res: any): any => {
          console.log('get file list succ')
          if (res.errno === 0) {
            const genFileList = (list: any[]) =>
              list.map(
                (item: any): BaiduFileItem => ({
                  id: item.fs_id,
                  filename: item.server_filename,
                  filetype: item.isdir ? '' : item.server_filename.split('.').pop(),
                  size: item.size || 0,
                  isDir: !!item.isdir,
                  isDelete: item.isdelete || '',
                  status: item.status || '',
                  category: item.category || '',
                  deteteId: item.delete_fs_id || '',
                  path: item.path,
                  md5: item.md5,
                  children: Array.isArray(item.enfi_children)
                    ? genFileList(item.enfi_children)
                    : item.enfi_children,
                }),
              )
            this.fileList = genFileList(res.list)
            return this.fileList
          }
          return Promise.reject({
            code: ERR_CODES.GET_LINK_EXTRACT_FAILED,
            baiduErrno: res.errno,
            msg: '',
          })
        },
      )
      .catch((err: any) => {
        if (!err) {
          return Promise.reject(err)
        }
        if (
          err.code === ERR_CODES.LADDER_SERVER_BUSY ||
          err.code === ERR_CODES.LADDER_SERVER_ERROR ||
          err.code === ERR_CODES.LADDER_URL_ERROR
        ) {
          console.log('ladder is unavailable')
          // this.isFromLadder = false
          // return this.getFileList()
          return Promise.reject({
            code: ERR_CODES.GET_LINK_NO_LADDERS,
          })
          // return Promise.reject({
          //   code: ERR_CODES.GET_LINK_EXTRACT_FAILED,
          //   msg: '',
          // })
        }
        if (err.code === ERR_CODES.LADDER_URL_INVALID) {
          return Promise.reject({
            code: ERR_CODES.GET_LINK_SHARE_URL_BANNED,
            msg: '',
          })
        }
        if (err.code === ERR_CODES.LADDER_WRONG_SHARECODE) {
          return Promise.reject({
            code: ERR_CODES.GET_LINK_NEED_EXTRACT_CODE,
            msg: '',
          })
        }
        if (err.code === ERR_CODES.LADDER_WRONG_VERCODE) {
          return Promise.reject({
            code: ERR_CODES.GET_LINK_EXTRACT_CAPTCHA_WRONG,
            msg: '',
          })
        }
        if (err.code === ERR_CODES.LADDER_PARSE_FAILED) {
          return Promise.reject({
            code: ERR_CODES.GET_LINK_EXTRACT_FAILED,
            msg: '',
          })
        }
        return Promise.reject(err)
      })
  }

  extract (code: string, verCode: string, verCodeStr: string): Promise<boolean> {
    console.log('extracting file')
    this.shareCode = code
    return baiduReq
      .req({
        url: `https://pan.baidu.com/share/verify?surl=${this.surl}&t=${Date.now()}&${
          this.extraUrlParams
        }&bdstoken=${this.yunData.bdstoken || 'null'}&logid=${genBaiduLogId(
          this.BAIDUID,
        )}=&clienttype=0`,
        method: 'POST',
        responseType: 'json',
        data: qs.stringify({
          pwd: code,
          vcode: verCode,
          vcode_str: verCodeStr,
        }),
      })
      .then(
        (res: any): boolean | PromiseLike<any> => {
          console.log(JSON.stringify(res.data))
          console.log(res.data.errno)
          if (res.data.errno === -9) {
            // 
            return Promise.reject({
              code: ERR_CODES.GET_LINK_EXTRACT_CODE_WRONG,
              msg: '',
            })
          }
          if (res.data.errno === -62) {
            // 
            return Promise.reject({
              code: ERR_CODES.GET_LINK_EXTRACT_CAPTCHA_WRONG,
              msg: '',
            })
          }
          if (res.data.errno === 0 || res.data.errno === 105) {
            return true
          }
          return Promise.reject({
            code: ERR_CODES.GET_LINK_EXTRACT_FAILED,
            msg: '',
            errObj: res.data,
          })
        },
      )
      .catch((err: any) => {
        console.error('extract file failed')
        console.error(err)
        return Promise.reject(err)
      })
  }

  getCaptcha (): Promise<{ img: string; str: string }> {
    return baiduReq
      .req({
        url: `https://pan.baidu.com/api/getcaptcha?prod=shareverify&web=1&t=${Math.random()}&${
          this.extraUrlParams
        }&bdstoken=null&logid=${genBaiduLogId(this.BAIDUID)}&clienttype=0`,
        method: 'GET',
      })
      .then(
        (res: any): any => {
          if (res.data.errno === 0) {
            return { img: res.data.vcode_img, str: res.data.vcode_str }
          }
          return Promise.reject({
            code: ERR_CODES.GET_LINK_GETCAPTCHA_FAILED,
            msg: '',
            errObj: res.data,
          })
        },
      )
      .catch((err: any) => {
        console.error('get extract captcha failed')
        console.error(err)
        return Promise.reject(err)
      })
  }

  getDownloadCaptcha (): Promise<{ img: string; str: string }> {
    console.log('getting download captcha')
    // 'https://pan.baidu.com/api/getvcode?prod=pan&t=0.8087614636544309&bdstoken=34b5cd8f6b510a4e22e1e4db5e87cc35&channel=chunlei&clienttype=0&web=1&app_id=265486&logid=MTU1NjExMTU1NDUzODAuNTk2NjY4ODQ2OTM0ODUy'
    return baiduReq
      .req({
        url: `https://pan.baidu.com/api/getvcode?prod=pan&t=${Math.random()}&${
          this.extraUrlParams
        }&bdstoken=${this.yunData.bdstoken}&logid=${genBaiduLogId(
          this.BAIDUID,
        )}&clienttype=0`,
        method: 'GET',
      })
      .then(
        (res: any): any => {
          if (res.data.errno === 0) {
            return { img: res.data.img, str: res.data.vcode }
          }
          return Promise.reject({
            code: ERR_CODES.GET_LINK_GETCAPTCHA_FAILED,
            msg: '',
            errObj: res.data,
          })
        },
      )
      .catch((err: any) => {
        console.error('get download link captcha failed')
        console.error(err)
        return Promise.reject(err)
      })
  }

  static getUrlParams () {
    return baiduReq
      .req({
        url: `https://pan.baidu.com/sns/box-static/disk-share/js/boot.js?t=${Date.now()}`,
        method: 'GET',
        headers: {
          Accept: '*/*',
        },
      })
      .then((res: any) => {
        const urlParamsReg = /ajaxSend\([\s\S]*?"[&|?](channel=.*?app_id=\d*)"/
        const urlParamsMatch = res.data.match(urlParamsReg)
        if (urlParamsMatch !== null) {
          return urlParamsMatch[1]
        } else {
          return Promise.resolve('channel=chunlei&web=1&app_id=250528')
        }
      })
      .catch((err: any) => {
        console.error('get url params failed')
        console.error(err)
        return Promise.resolve('channel=chunlei&web=1&app_id=250528')
      })
  }
}

export default BaiduFileGetter
