import Vue from 'vue'
import VueI18n from 'vue-i18n'
import '@/plugins/iview'
import '@/assets/css/_base.scss'
import '@/plugins/ga'
import '@/utils/zhuge'
import { addMessageListener, postMessage } from '@/utils/messaging'
import * as MESSAGES from '@/constants/messages'
import EventHub from '@/components/EventHub'

Vue.use(VueI18n)
Vue.config.productionTip = false
Vue.config.devtools = process.env.NODE_ENV === 'development'

if (/Electron/.test(navigator.userAgent)) {
  window.context = 'electron'
}
if (/enfi-cloud-android/.test(navigator.userAgent) || /Android/.test(navigator.userAgent)) {
  document.body.classList.add('android')
  window.context = 'android'
  window.androidMsgListener = {}
  window.receiveAndroidMessage = (data: any) => {
    console.log('android message received')
    console.log(data)
    let msgData: { message: string, data: any } = data
    try {
      // msgData = JSON.parse(data)
      if (!msgData.message || msgData.message.length === 0) {
        throw new Error('no "message" in msgData')
      }
      const handlers = window.androidMsgListener[msgData.message]
      if (handlers) {
        handlers.forEach((handler: (data?: any) => void) => {
          handler(msgData.data)
        })
      }
    } catch (err) {
      console.error('android message parse failed')
      console.error(err)
    }
  }
}

// receive init data from the client
addMessageListener(MESSAGES.INIT, (data: any) => {
  console.log('received init message')
  window.initData(data)
})

window.ladderApiVer = 'v3'
window.initData = (data: any) => {
  console.log('init window data')
  console.log(data)
  window.appver = data.appver
  window.platform = data.platform
  window.enfiToken = data.enfiToken
  window.enfiUid = data.enfiUid
  window.deviceId = data.deviceId
  if (data.ladderApiVer === 'v2') {
    window.ladderApiVer = 'v2'
  }
  EventHub.$emit('init-data')
}

postMessage(MESSAGES.PAGE_LOADED)

window.addEventListener('DOMContentLoaded', () => {
  postMessage(MESSAGES.PAGE_LOADED)
})
window.onload = () => {
  postMessage(MESSAGES.PAGE_LOADED)
}
