export const postMessage = (message: string, data?: any) => {
  console.log(`post message ${message}`)
  if (window.context === 'android') {
    return window.enfiAndroid.receiveWebMessage(JSON.stringify({ message, data }))
  }
  if (window.context === 'electron') {
    return window.sendIpcMessage(message, data)
  }
}

export const addMessageListener = (
  message: string,
  callback: (data?: any) => void,
  configs: { once: boolean } = { once: false },
) => {
  if (window.context === 'android') {
    if (!window.androidMsgListener[message]) {
      window.androidMsgListener[message] = []
    }
    if (configs && configs.once) {
      const handler = (data: any) => {
        callback(data)
        const idx = window.androidMsgListener[message].indexOf(handler)
        window.androidMsgListener[message].splice(idx, 1)
      }
      window.androidMsgListener[message].push(handler)
      return handler
    } else {
      window.androidMsgListener[message].push(callback)
      return callback
    }
  }
  if (window.context === 'electron') {
    return window.addIpcListener(message, (data: any) => {
      console.log(`received message ${message}`)
      console.log(data)
      callback(data)
    }, configs)
  }
}

export const removeMessageListener = (message: string, handlerOrIsAll?: boolean | ((data?: any) => void)) => {
  if (window.context === 'android') {
    if (handlerOrIsAll && handlerOrIsAll === true) {
      window.androidMsgListener[message] = []
    } else if (typeof handlerOrIsAll === 'function') {
      const idx = window.androidMsgListener[message].indexOf(handlerOrIsAll)
      window.androidMsgListener[message].splice(idx, 1)
    }
  }
  if (window.context === 'electron') {
    if (handlerOrIsAll && handlerOrIsAll === true) {
      window.removeAllIpcListeners(message)
    } else if (typeof handlerOrIsAll === 'function') {
      window.removeIpcListener('message', handlerOrIsAll)
    }
  }
}
