







































import Vue from 'vue'
import { Tree } from 'iview'
import filesize from 'filesize'
import { Component, Prop, Watch } from 'vue-property-decorator'
import FlatTree from '@/components/common/FlatTree/FlatTree.vue'
import { FileItem } from '@/components/types'

Vue.component('FlatTree', FlatTree)

@Component({})
export default class App extends Vue {
  treeData: any[] = []
  allChecked: boolean = false
  indeterminate: boolean = true
  checkedFiles: FileItem[] = []
  checkedNodes: any[] = []
  treeDataEmpty: boolean = false

  @Prop({ default: () => [] }) fileList!: FileItem[]

  @Watch('fileList')
  onFilelistChange (val: FileItem[]) {
    console.log('file list changed')
    console.log(val)
  }

  get filesCount () {
    let filesCount = 0
    const countFiles = (fileList: FileItem[]) => {
      fileList.forEach((file: FileItem) => {
        if (!file.isDir) {
          filesCount += 1
        } else if (file.children) {
          countFiles(file.children)
        }
      })
    }
    countFiles(this.fileList)
    return filesCount
  }

  clearData () {
    this.allChecked = false
    this.treeData = []
    this.indeterminate = true
    this.checkedFiles = []
    this.checkedNodes = []
  }

  genTreeData () {
    console.log('generating file tree')
    console.log(this.fileList)
    const treeDataGenerator = (fileList: FileItem[]): any[] =>
      fileList.map((fileItem: FileItem) => {
        if (!fileItem.isDir) {
          return {
            title: fileItem.filename,
            attr: fileItem,
            checked: true,
            render: (h: any, params: { root: any; node: any; data: any }) =>
              h &&
              h(
                'span',
                {
                  class: 'tree-node',
                  style: {
                    display: 'inline-block',
                    width: '100%',
                  },
                },
                [
                  h('span', { class: 'tree-filename', attrs: { title: params.data.title } }, [params.data.title]),
                  h('span', { class: 'tree-filetype' }, [fileItem!.filetype]),
                  h('span', { class: 'tree-filesize' }, [filesize(fileItem.size!)]),
                ]
              ),
          }
        }
        return {
          title: fileItem.filename,
          attr: fileItem,
          expand: true,
          render: (h: any) =>
            h(
              'span',
              {
                class: 'tree-node',
                style: {
                  display: 'inline-block',
                  width: '100%',
                },
              },
              [h('span', { attrs: { title: fileItem.filename } }, [fileItem.filename])]
            ),
          children: treeDataGenerator(fileItem.children || []),
        }
      })
    this.allChecked = true
    this.treeData = treeDataGenerator(this.fileList)
    console.log('file tree generated')
    console.log(this.treeData)
    this.treeDataEmpty = this.treeData.length === 0
    this.$nextTick(() => {
      this.onCheck()
    })
  }

  handleCheckAll () {
    if (this.treeData.length === 0) {
      return
    }

    if (this.indeterminate) {
      this.allChecked = false
    } else {
      this.allChecked = !this.allChecked
    }
    this.indeterminate = false

    const checkAll = (data: any[], toCheck: boolean) =>
      data.map((item: any) => {
        if (item.attr.isDir) {
          let children = item.children
          if (children && children.length > 0) {
            children = checkAll(children, toCheck)
            return Object.assign(item, { children, checked: toCheck })
          }
        }
        return Object.assign(item, { checked: toCheck })
      })

    this.treeData = checkAll(this.treeData, this.allChecked)
    this.$nextTick(() => {
      this.onCheck()
    })
  }

  onCheck () {
    console.log('tree check fired')
    if (!this.$refs['file-tree']) {
      return
    }
    // $refs['file-tree'] might not exist, add a timer
    setTimeout(() => {
      this.checkedNodes = (this.$refs['file-tree'] as Tree)
        .getCheckedNodes()
        .filter((node: any) => !node.attr.isDir)
      this.checkedFiles = this.getCheckedFiles()
      if (this.checkedNodes.length === this.filesCount) {
        this.indeterminate = false
        this.allChecked = true
      } else if (this.checkedNodes.length > 0) {
        this.indeterminate = true
        this.allChecked = false
      } else {
        this.indeterminate = false
        this.allChecked = false
      }
      this.$emit('check', this.checkedFiles)
    }, 100)
  }

  getCheckedFiles () {
    console.log('getting checked file tree')
    const genCheckedFiles = (dataList: any[]): FileItem[] => {
      const checkedFiles: FileItem[] = []
      dataList.forEach((item: any) => {
        if (item.children === undefined) {
          if (item.checked) {
            checkedFiles.push(item.attr as FileItem)
          }
        } else if (item.children.length > 0 && (item.indeterminate || item.checked)) {
          const dirItem = item.attr as FileItem
          dirItem.children = genCheckedFiles(item.children)
          checkedFiles.push(dirItem)
        }
      })
      return checkedFiles
    }
    return genCheckedFiles(this.treeData)
  }
}
