import { BTFileItem } from '@/components/types'

export const btFileConverter = (files: any[]): BTFileItem[] => {
  const fileTree: BTFileItem[] = []
  files.forEach((file: any, fileIdx: number) => {
    const paths = file.path.split('/')
    let curFileList: BTFileItem[] | BTFileItem = fileTree
    let curPath = ''
    for (let i = 0; i < paths.length; i++) {
      const isLastPath = i === paths.length - 1
      const curList: BTFileItem[] | undefined = Array.isArray(curFileList) ? curFileList : (curFileList as BTFileItem).children
      if (!curList) {
        break
      }
      let node: BTFileItem | undefined = curList!.find((subFile: BTFileItem) => subFile.filename === paths[i])
      const isDir = paths.length > 1 && !isLastPath
      if (!node) {
        const filename = isLastPath ? file.filename : paths[i]
        if (curPath.length === 0) {
          curPath += paths[i]
        } else {
          curPath += `/${paths[i]}`
        }
        node = {
          filename: filename,
          isDir,
          path: curPath,
        }
        if (isDir) {
          node.children = []
        } else {
          node.id = fileIdx + 1
          node.filetype = file.filename.split('.').slice(-1)[0]
          node.size = file.size
        }
        curList.push(node)
      }
      if (node && node.isDir && node.children) {
        curFileList = node
      }
    }
  })
  return fileTree
}
