import CryptoJS from 'crypto-js'
import merge from 'lodash.merge'
import * as MESSAGES from '@/constants/messages'
import { postMessage, addMessageListener } from '@/utils/messaging'
import { BAIDU_UA } from '@/constants/constants'

export default {
  defaultOptons: {
    method: 'GET',
    headers: {
      'User-Agent': BAIDU_UA,
      Host: 'pan.baidu.com',
      Connection: 'keep-alive',
      Origin: 'https://pan.baidu.com',
      'X-Requested-With': 'XMLHttpRequest',
    },
  },

  setDefaults (defaultOptons: { [key: string]: any }) {
    const setOption = (target: { [key: string]: any }, src: { [key: string]: any }) => {
      for (let key in src) {
        if (src.hasOwnProperty(key)) {
          if (typeof src[key] === 'object') {
            if (!target[key]) {
              target[key] = {}
            }
            setOption(target[key], src[key])
          } else {
            target[key] = src[key]
          }
        }
      }
    }
    setOption(this.defaultOptons, defaultOptons)
  },

  req (params: any) {
    console.log('requesting baidu url')
    console.log(params)
    return new Promise((resolve, reject) => {
      const reqId = CryptoJS.lib.WordArray.random(16)

      postMessage(`${MESSAGES.URL_REQUEST}-${reqId}`, merge({}, this.defaultOptons, params))
      const receiveMessage = (data: any) => {
        console.log('iframe url-response message received')
        console.log(data)
        if (data.code === 0) {
          return resolve(data)
        }
        return reject(data)
      }
      addMessageListener(`${MESSAGES.URL_RESPONSE}-${reqId}`, receiveMessage, { once: true })
    })
  },
}
