/**
 * version compare func
 * @param ver1{String} 'x.x.x'
 * @param ver2{String} 'x.x.x'
 * @return ver1 > ver2: 1; ver1 < ver2: -1; ver1 === ver2: 0
 */

const versionCompare = (ver1: string, ver2: string) => {
  console.log(`comparing version ${ver1}, ${ver2}`)
  const ver1Arr = ver1.split('.').map((item: string) => parseInt(item))
  const ver2Arr = ver2.split('.').map((item: string) => parseInt(item))
  if (ver1Arr[0] > ver2Arr[0]) {
    return 1
  }
  if (ver1Arr[0] < ver2Arr[0]) {
    return -1
  }
  if (ver1Arr[1] > ver2Arr[1]) {
    return 1
  }
  if (ver1Arr[1] < ver2Arr[1]) {
    return -1
  }
  if (isNaN(Number(ver2Arr[2]))) {
    return 1
  }
  if (ver1Arr[2] > ver2Arr[2]) {
    return 1
  }
  if (ver1Arr[2] < ver2Arr[2]) {
    return -1
  }
  return 0
}

export const versionCompareWithMethod = (
  currVer: string,
  localVer: string,
  method: 'gt' | 'lt' | 'gte' | 'lte' | 'e',
) => {
  console.log(currVer, localVer)
  if (!currVer || !localVer) {
    return false
  }
  const compareRes = versionCompare(currVer, localVer)
  if (method === 'gt') {
    return compareRes === 1
  }
  if (method === 'lt') {
    return compareRes === -1
  }
  if (method === 'e') {
    return compareRes === 0
  }
  if (method === 'gte') {
    return compareRes === 1 || compareRes === 0
  }
  if (method === 'lte') {
    return compareRes === -1 || compareRes === 0
  }
  return false
}

export default versionCompare
