import axios, { AxiosResponse } from 'axios'
import { SERVER_HOST } from '@/constants/constants'
import { versionCompareWithMethod } from '@/utils/versionCompare'

export const getOfflineChecker = () => {
  console.log('getting offline checker')
  console.log(window.appver)
  let reqUrl = ''
  if (versionCompareWithMethod('2.4.0', window.appver, 'lte')) {
    reqUrl = `${SERVER_HOST}/api/v2/offlineDl/consume`
  } else {
    reqUrl = `${SERVER_HOST}/api/offlineDl/apply`
  }
  return axios({
    url: `${reqUrl}?plt=${window.platform}&appv=${window.appver}&ref=bdp&enfi-uid=${window.enfiUid}&enfi-token=${window.enfiToken}`,
    method: 'POST',
    headers: {
      Authorization: window.enfiToken,
    },
    data: {
      deviceId: window.deviceId,
      quantity: 1,
      platform: window.context === 'android' ? 'android' : 'pc',
    },
  })
    .then((res: AxiosResponse<any>) => {
      if (res.data.code === 0) {
        return res.data.data.checker
      }
      console.error('get checker failed')
      console.error(res.data)
      return Promise.reject(res.data)
    })
    .catch((err: any) => {
      console.error('get checker failed')
      console.error(err)
      return Promise.reject(err)
    })
}
