

































import Vue from 'vue'
import { Component } from 'vue-property-decorator'
import parseTorrent from 'parse-torrent'
import * as ERR_CODES from '@/constants/errCodes'
import * as GA_EVENTS from '@/constants/gaEvents'
import * as ZHUGE_EVENTS from '@/constants/zhugeIOEvents'
import * as MESSAGES from '@/constants/messages'
import { postMessage, addMessageListener } from '@/utils/messaging'
import { createOfflineTask as createOfflineTaskSvc } from '@/services/ladder'

const magnetRegex = /magnet:\?xt=urn:\S{20,50}/

@Component({})
export default class App extends Vue {
  showDragHint: boolean = false
  heightWatcher: number = 0
  inputLink: string = ''
  magnetLinkToOffline: string = ''
  parsing: boolean = false
  creatingOfflineTask: boolean = false
  errMsg: string = ''

  mounted () {
    addMessageListener(MESSAGES.ON_HIDE, () => {
      this.clearData()
    })
    addMessageListener(MESSAGES.DO_CREATE_OFFLINE_TASK, (data?: any) => {
      console.log('do-create-offline-task received')
      console.log(data)
      if (typeof data === 'string') {
        this.createOfflineTask(data)
      } else if (data && data.url) {
        this.createOfflineTask(data.url, data.name)
      } else if (data === null) {
        this.createOfflineTask(this.inputLink)
      } else {
        this.errMsg = ''
      }
    })

    let lastScrollHeight = document.body.scrollHeight
    const watch = () => {
      clearTimeout(this.heightWatcher)
      if (lastScrollHeight !== document.body.scrollHeight) {
        console.log('height changed ', document.body.scrollHeight)
        postMessage(MESSAGES.ADJUST_HEIGHT, { pageHeight: document.body.scrollHeight })
      }
      lastScrollHeight = document.body.scrollHeight
      this.heightWatcher = setTimeout(() => {
        watch()
      }, 200)
    }
    this.heightWatcher = setTimeout(() => {
      watch()
    }, 200)
  }

  clearData () {
    this.showDragHint = false
    this.inputLink = ''
    this.magnetLinkToOffline = ''
    this.creatingOfflineTask = false
    this.parsing = false
    this.errMsg = ''
  }

  onLinkPasted () {
    console.log('link pasted ', this.inputLink)
    setTimeout(() => {
      this.createOfflineTask(this.inputLink)
    }, 200)
  }

  onContextMenu (e: Event) {
    e.preventDefault()
    postMessage(MESSAGES.RIGHT_CLICK)
  }

  handleDragOver (e: any) {
    e.preventDefault()
    this.showDragHint = true
    e.dataTransfer.dropEffect = 'copy'
  }
  handleDragLeave (e: any) {
    e.preventDefault()
    this.showDragHint = false
  }
  handleDrop (e: any) {
    e.preventDefault()
    console.log('handle drop')
    this.showDragHint = false
    console.log(e.dataTransfer.files[0].name)
    if (
      e.dataTransfer.files.length === 1 &&
      e.dataTransfer.files[0].name.split('.').slice(-1)[0] === 'torrent'
    ) {
      this.$gaVisitor
        .event(
          GA_EVENTS.EVENT_OFFLINE_START_PARSE_TORRENT,
          GA_EVENTS.EVENT_TYPE_EVT,
        )
      window.zhuge.track(ZHUGE_EVENTS.EVENT_OFFLINE_START_PARSE_TORRENT)

      postMessage(MESSAGES.START_PARSING_TORRENT)
      this.parsing = true
      const file = e.dataTransfer.files[0]
      console.log(file.path)
      // const reader = new FileReader()
      console.log(file)

      parseTorrent.remote(file, (err: Error, parsedTorrent: any) => {
        if (err) {
          postMessage(MESSAGES.PARSING_TORRENT_FAILED)
          this.parsing = false
          console.error(err)
          this.errMsg = ''
          return
        }
        console.log(parsedTorrent)
        if (parsedTorrent.infoHash) {
          const magUri = parseTorrent.toMagnetURI({
            infoHash: parsedTorrent.infoHash,
          })
          console.log(magUri)
          postMessage(MESSAGES.PARSING_TORRENT_SUCC)
          this.$gaVisitor
            .event(
              GA_EVENTS.EVENT_OFFLINE_PARSE_TORRENT_SUCC,
              GA_EVENTS.EVENT_TYPE_EVT,
            )
          window.zhuge.track(ZHUGE_EVENTS.EVENT_OFFLINE_PARSE_TORRENT_SUCC)

          this.parsing = false
          this.createOfflineTask(magUri, parsedTorrent.name || file.name)
        } else {
          postMessage(MESSAGES.PARSING_TORRENT_FAILED)
          this.$gaVisitor
            .event(
              GA_EVENTS.EVENT_OFFLINE_PARSE_TORRENT_FAILED,
              GA_EVENTS.EVENT_TYPE_EVT,
            )
          window.zhuge.track(ZHUGE_EVENTS.EVENT_OFFLINE_PARSE_TORRENT_FAILED)
          this.parsing = false
          console.error('no infoHash')
          this.errMsg = ''
        }
      })
    } else {
      this.errMsg = ' .torrent '
    }
  }

  createOfflineTask (magnetUri: string, name?: string) {
    if (this.creatingOfflineTask) {
      return
    }
    console.log('creating offline task ', magnetUri, name)
    if (!magnetRegex.test(magnetUri)) {
      this.$gaVisitor
        .event(
          GA_EVENTS.EVENT_OFFLINE_MAGNET_INVALID,
          GA_EVENTS.EVENT_TYPE_EVT,
          magnetUri
        )
      window.zhuge.track(ZHUGE_EVENTS.EVENT_OFFLINE_MAGNET_INVALID, {
        magnetUri,
      })
      this.errMsg = ''
      return
    }
    this.creatingOfflineTask = true
    this.inputLink = magnetUri

    const createTask = () => {
      postMessage(MESSAGES.START_CREATING_OFFLINE_TASK)
      this.$gaVisitor
        .event(
          GA_EVENTS.EVENT_OFFLINE_CREATE_TASK,
          GA_EVENTS.EVENT_TYPE_EVT,
        )
      window.zhuge.track(ZHUGE_EVENTS.EVENT_OFFLINE_CREATE_TASK)

      createOfflineTaskSvc({
        magnetUri: magnetUri,
        name: name,
      })
        .then((res: any) => {
          this.errMsg = ''
          this.creatingOfflineTask = false
          postMessage(MESSAGES.CREATING_OFFLINE_TASK_SUCC)
          this.$gaVisitor
            .event(
              GA_EVENTS.EVENT_OFFLINE_CREATE_TASK_SUCC,
              GA_EVENTS.EVENT_TYPE_EVT,
            )
          window.zhuge.track(ZHUGE_EVENTS.EVENT_OFFLINE_CREATE_TASK_SUCC)
          return true
        })
        .catch((err: any) => {
          console.error('create offline task failed')
          console.error(err)
          this.creatingOfflineTask = false
          postMessage(MESSAGES.CREATING_OFFLINE_TASK_FAILED, err)

          this.$gaVisitor
            .event(
              GA_EVENTS.EVENT_OFFLINE_CREATE_TASK_FAIL,
              GA_EVENTS.EVENT_TYPE_EVT,
              err.code,
            )
          window.zhuge.track(ZHUGE_EVENTS.EVENT_OFFLINE_CREATE_TASK_FAIL, {
            errCode: err.code,
            errMsg: err.message,
          })

          if (
            err.code === ERR_CODES.LADDER_SERVER_BUSY ||
            err.code === ERR_CODES.LADDER_SERVER_ERROR
          ) {
            this.errMsg = ''
          } else if (err.code === ERR_CODES.LADDER_OFFLINE_ALREADY_FAILED) {
            this.errMsg = ''
          } else {
            this.errMsg = ''
          }
        })
    }

    postMessage(MESSAGES.REQUEST_CREATE_OFFLINE_CONFIRM)
    addMessageListener(MESSAGES.CONFIRM_CREATE_OFFLINE_TASK, (data: any) => {
      console.log('create offline confirmed')
      if (data === true) {
        createTask()
      } else {
        this.creatingOfflineTask = false
      }
    }, { once: true })
  }
}
