















































































































import Vue from 'vue'
import { Component } from 'vue-property-decorator'
import filesize from 'filesize'
import BaiduFileGetter from '@/services/BaiduFileGetter'
import { MAX_DL_FILES } from '@/constants/constants'
import * as ERR_CODES from '@/constants/errCodes'
import * as GA_EVENTS from '@/constants/gaEvents'
import * as ZHUGE_EVENTS from '@/constants/zhugeIOEvents'
import * as MESSAGES from '@/constants/messages'
import { postMessage, addMessageListener } from '@/utils/messaging'
import FileSelector from '@/components/FileSelector.vue'
import { btFileConverter } from '@/utils/torrentParser'
import { BaiduFileItem, FileItem } from '@/components/types'
import { versionCompareWithMethod } from '@/utils/versionCompare'
import EventHub from '@/components/EventHub'
import { reportResourceValidity } from '@/services/tracker'

const baiduLinkRegex = /(https?:\/\/(pan|yun).baidu.com\/(s|wap|share)\/[\x00-\xff]*)/
const baiduLinkWithCodeRegex = /(https?:\/\/(pan|yun)\.baidu\.com\/(s|wap|share)\/[\x00-\xff]*?)\s*\S?(()|())[:]\s?(\w{4})/
const magnetRegex = /magnet:\?xt=urn:\S{20,}/
const httpLinkRegex = /https?:\/\/(www\.)?[-a-zA-Z0-9@:%._+~#=]{1,256}\.[a-zA-Z0-9()]{1,6}\b([-a-zA-Z0-9()@:%_+.~#?&//=]*)/

const calcSize = (fileList: FileItem[]): number =>
  fileList.reduce((acc: number, cur: FileItem) => {
    if (!cur.isDir) {
      return acc + cur.size!
    } else if (cur.children && cur.children.length > 0) {
      return acc + calcSize(cur.children)
    }
    return acc
  }, 0)

@Component({ components: { FileSelector } })
export default class App extends Vue {
  taskType: number = 0 // 0 for baidu, 1 for bt, 2 for http
  heightWatcher: number = 0
  inputLink: string = ''
  shareLink: string = ''
  gettingFiles: boolean = false
  needExtractCode: boolean = false
  inputExtractCode: string = ''
  extractCode: string = ''
  isExtractCodeCorrect: boolean = false
  extractNeedVercode: boolean = false
  extractVerCodeSrc: string = ''
  extractVerCode: string = ''
  verCodeStr: string = ''
  errMsg: string = ''
  showFileSelector: boolean = false
  fileGetter: BaiduFileGetter = new BaiduFileGetter()
  fileList: FileItem[] = []
  checkedFiles: FileItem[] = []

  parsingMagnetUri: boolean = false
  BTParseFinished: boolean = false
  parseMagnetTimeout: boolean = false
  parsingHTTPUrl: boolean = false
  HTTPParseFinished: boolean = false

  clientVersion: string = window.appver
  inputPlaceHolder: string = ''
  wrongUrlHint: string = ''

  get filesCount () {
    let filesCount = 0
    const countFiles = (fileList: FileItem[]) => {
      fileList.forEach((file: FileItem) => {
        if (!file.isDir) {
          filesCount += 1
        } else if (file.children) {
          countFiles(file.children)
        }
      })
    }
    countFiles(this.fileList)
    return filesCount
  }

  get totalFileSize (): number {
    return calcSize(this.fileList)
  }
  get selectedFileSizeSum (): number {
    return calcSize(this.checkedFiles)
  }

  get displaySumSize () {
    if (this.isSingleFile) {
      return filesize(this.fileList[0].size as number)
    }
    return filesize(this.selectedFileSizeSum)
  }

  get displayTotalSize () {
    if (this.isSingleFile) {
      return filesize(this.fileList[0].size as number)
    }
    return filesize(this.totalFileSize)
  }

  get isSingleFile () {
    return this.fileList.length === 1 && !this.fileList[0].isDir
  }

  get checkedFilesCount () {
    let filesCount = 0
    const countFiles = (fileList: FileItem[]) => {
      fileList.forEach((file: FileItem) => {
        if (!file.isDir) {
          filesCount += 1
        } else if (file.children && file.children.length > 0) {
          countFiles(file.children)
        }
      })
    }
    countFiles(this.checkedFiles)
    return filesCount
  }

  get BTName () {
    if (this.BTParseFinished && this.fileList.length > 0) {
      return this.fileList[0].path.split('/')[0]
    }
    return ''
  }

  mounted () {
    const setTexts = () => {
      const verCompResult = versionCompareWithMethod('2.3.0', this.clientVersion, 'lte')
      console.log(verCompResult)
      if (verCompResult) {
        this.inputPlaceHolder = 'HTTP'
        this.wrongUrlHint = ' http '
      } else {
        this.inputPlaceHolder = ''
        this.wrongUrlHint = ''
      }
    }
    setTexts()
    EventHub.$on('init-data', () => {
      console.log('client data inited!!')
      console.log(window.appver)
      this.clientVersion = window.appver
      setTexts()
    })

    addMessageListener(MESSAGES.ON_SHOW, () => {
      console.log('on-show')
      console.log(this.gettingFiles)
      this.fileGetter.clearData()
    })
    addMessageListener(MESSAGES.ON_HIDE, () => {
      this.clearData()
    })
    addMessageListener(MESSAGES.DO_EXTRACT, (data?: any) => {
      console.log('do-extract received')
      console.log(data)
      this.showFileSelector = false
      if (data && data.copyText) {
        this.inputLink = data.copyText
      } else if (data && data.shareUrl) {
        this.inputLink = data.shareUrl
        if (data.shareCode.length > 0) {
          this.inputExtractCode = data.shareCode
          this.needExtractCode = true
        } else {
          this.needExtractCode = false
          this.inputExtractCode = ''
        }
      }
      this.parseLink()
    })
    addMessageListener(MESSAGES.SEND_MAGNET_URI_RESULT, this.onMagnetResult)
    addMessageListener(MESSAGES.HTTP_FILE_INFO_RESULT, this.onHttpResult)
    addMessageListener(MESSAGES.SEND_MAGNET_URI_TIMEOUT, () => {
      this.parseMagnetTimeout = true
    })

    let lastScrollHeight = document.body.scrollHeight
    const watch = () => {
      clearTimeout(this.heightWatcher)
      if (lastScrollHeight !== document.body.scrollHeight) {
        console.log('height changed ', document.body.scrollHeight)
        postMessage(MESSAGES.ADJUST_HEIGHT, { pageHeight: document.body.scrollHeight })
      }
      lastScrollHeight = document.body.scrollHeight
      this.heightWatcher = setTimeout(() => {
        watch()
      }, 200)
    }
    this.heightWatcher = setTimeout(() => {
      watch()
    }, 200)
  }

  onContextMenu (e: Event) {
    e.preventDefault()
    postMessage(MESSAGES.RIGHT_CLICK)
  }

  hasFilename (filename: string, ext: string) {
    //  parse 
    if (ext.length > 0) {
      return filename.split(`.${ext}`)[0].length > 0
    }
    return true
  }

  onLinkPasted () {
    console.log('link pasted ', this.inputLink)
    setTimeout(() => {
      this.clearTaskData()
      this.parseLink()
    }, 200)
  }

  clearData () {
    console.log('clearing component data')
    this.clearTaskData()
    this.gettingFiles = false
    this.inputLink = ''
    this.shareLink = ''
    this.BTParseFinished = false
    this.parsingMagnetUri = false
    this.parseMagnetTimeout = false
    this.HTTPParseFinished = false
    this.parsingHTTPUrl = false
  }

  clearTaskData () {
    console.log('clearing task data')
    this.inputExtractCode = ''
    this.needExtractCode = false
    this.isExtractCodeCorrect = false
    this.extractCode = ''
    this.extractNeedVercode = false
    this.extractVerCode = ''
    this.verCodeStr = ''
    this.fileList = []
    this.checkedFiles = []
    this.gettingFiles = false
    this.showFileSelector = false
    this.errMsg = ''

    const fileSelector = this.$refs['file-selector'] as FileSelector
    if (fileSelector) {
      fileSelector.clearData()
    }
  }

  parseLink (e?: Event) {
    if (e) {
      e.preventDefault()
    }

    console.log(this.inputLink)
    if (versionCompareWithMethod('2.3.0', this.clientVersion, 'lte')) {
      let magnetMatch = this.inputLink.match(magnetRegex)
      if (magnetMatch) {
        return this.handleMagnetUri()
      }
    }

    let linkMatch = this.inputLink.match(baiduLinkWithCodeRegex)
    const oldLink = this.shareLink
    console.log('matching link')
    if (linkMatch) {
      this.$gaVisitor
        .event(GA_EVENTS.EVENT_START_PARSING_SHARE_URL, GA_EVENTS.EVENT_TYPE_EVT)
      window.zhuge.track(ZHUGE_EVENTS.EVENT_START_PARSING_SHARE_URL)
      console.log(linkMatch)
      this.shareLink = linkMatch[1].split('#')[0].trim()
      this.extractCode = linkMatch[7].trim()
    } else {
      linkMatch = this.inputLink.match(baiduLinkRegex)
      console.log(linkMatch)
      if (linkMatch) {
        this.$gaVisitor
          .event(GA_EVENTS.EVENT_START_PARSING_SHARE_URL, GA_EVENTS.EVENT_TYPE_EVT)
        window.zhuge.track(ZHUGE_EVENTS.EVENT_START_PARSING_SHARE_URL)
        this.shareLink = linkMatch[1].split('#')[0].trim()
        this.extractCode = ''
      } else {
        this.shareLink = ''
        this.extractCode = ''
      }
    }

    if (this.inputExtractCode.length > 0) {
      this.extractCode = this.inputExtractCode
    }
    if (this.shareLink !== oldLink) {
      this.isExtractCodeCorrect = false
    }
    if (this.shareLink.length === 0) {
      if (versionCompareWithMethod('2.3.0', this.clientVersion, 'lte')) {
        const httpLinkMatch = this.inputLink.match(httpLinkRegex)
        if (httpLinkMatch) {
          return this.handleHTTPUrl()
        }
      }
      this.errMsg = this.wrongUrlHint
      postMessage(MESSAGES.GET_FILES_FAILED, { error: { code: ERR_CODES.GET_LINK_URL_INVALID } })
      return
    }
    this.taskType = 0
    this.gettingFiles = true
    postMessage(MESSAGES.START_GETTING_FILES)
    this.errMsg = ''
    return Promise.resolve()
      .then(() => {
        if (!this.fileGetter || this.fileGetter.extraUrlParams.length === 0) {
          this.fileGetter = new BaiduFileGetter()
          return this.fileGetter.setUrl(this.shareLink)
        }
        return true
      })
      .then(() => {
        if (!this.isExtractCodeCorrect && this.extractCode.length > 0) {
          return this.fileGetter.extract(
            this.extractCode,
            this.extractVerCode,
            this.verCodeStr,
          )
        }
        return true
      })
      .then(() => {
        if (this.inputLink.length === 0) {
          // if the input link was cleared, means that the webview modal was closed, do nothing
          return []
        }
        this.isExtractCodeCorrect = true
        this.extractNeedVercode = false
        return this.fileGetter.getFileListFromLadder(this.extractVerCode, this.verCodeStr)
      })
      .then((fileList: BaiduFileItem[]): any => {
        if (this.inputLink.length === 0) {
          // if the input link was cleared, means that the webview modal was closed, do nothing
          return true
        }
        console.log(fileList)
        this.fileList = fileList
        this.errMsg = ''
        this.gettingFiles = false
        if (fileList.length === 0) {
          this.$gaVisitor
            .event(
              GA_EVENTS.EVENT_GET_FILE_EMPTY,
              GA_EVENTS.EVENT_TYPE_EVT,
              btoa(
                `url=${encodeURIComponent(this.shareLink)};sharecode=${
                  this.extractCode
                };`
              )
            )
          window.zhuge.track(ZHUGE_EVENTS.EVENT_GET_FILE_EMPTY, {
            link: this.shareLink,
            sharecode: this.extractCode,
          })
          this.clearTaskData()
          return Promise.reject({ code: -1 })
        } else {
          this.$gaVisitor
            .event(GA_EVENTS.EVENT_GET_FILE_SUCC, GA_EVENTS.EVENT_TYPE_EVT)
          window.zhuge.track(ZHUGE_EVENTS.EVENT_GET_FILE_SUCC)
          reportResourceValidity(this.shareLink, true, this.extractCode)

          if (this.filesCount > MAX_DL_FILES) {
            this.$gaVisitor
              .event(
                GA_EVENTS.EVENT_BAIDU_LINK_MASS_FILES,
                GA_EVENTS.EVENT_TYPE_EVT,
                btoa(
                  `url=${encodeURIComponent(this.shareLink)};sharecode=${
                    this.extractCode
                  };fileCount=${this.filesCount}`
                )
              )
            window.zhuge.track(ZHUGE_EVENTS.EVENT_BAIDU_LINK_MASS_FILES, {
              link: this.shareLink,
              sharecode: this.extractCode,
              fileCount: this.filesCount,
            })
          }
        }

        postMessage(MESSAGES.FINISH_GETTING_FILES, {
          files: this.fileList,
          shareUrl: this.fileGetter.shareUrl,
          shareCode: this.extractCode,
        })
        if (window.context === 'android') {
          return
        }
        if (this.isSingleFile) {
          this.checkedFiles = [this.fileList[0]]
          this.showFileSelector = true
          postMessage(MESSAGES.TREE_CHECKED, {
            taskType: this.taskType,
            files: this.checkedFiles,
          })
          return true
        } else {
          console.log('showing file selector')
          this.showFileSelector = true
          this.$nextTick(() => {
            console.log(this.fileList)
            const fileSelector = this.$refs['file-selector'] as FileSelector
            console.log(fileSelector)
            if (fileSelector) {
              fileSelector.genTreeData()
            }
          })
          return true
        }
      })
      .catch((err: any) => {
        console.error('get baidu file failed')
        console.error(err)
        this.gettingFiles = false
        postMessage(MESSAGES.GET_FILES_FAILED, { error: err })
        this.showFileSelector = false
        if (this.extractVerCode.length > 0) {
          this.getCaptcha()
        }
        switch (err.code) {
          case ERR_CODES.GET_LINK_URL_INVALID:
            console.log('set url failed, match surl failed')
            this.errMsg = err.msg
            this.$gaVisitor
              .event(
                GA_EVENTS.EVENT_GET_FILE_SHARE_URL_INVALID,
                GA_EVENTS.EVENT_TYPE_EVT,
                btoa(encodeURIComponent(this.inputLink))
              )
            window.zhuge.track(ZHUGE_EVENTS.EVENT_GET_FILE_SHARE_URL_INVALID, {
              link: this.inputLink || '',
            })
            break
          case ERR_CODES.INIT_NOT_COMPLETE:
            this.errMsg = ''
            postMessage(MESSAGES.GET_INIT_DATA)
            this.$gaVisitor.event(
              GA_EVENTS.EVENT_NO_INIT_LADDERS,
              GA_EVENTS.EVENT_TYPE_EVT
            )
            window.zhuge.track(ZHUGE_EVENTS.EVENT_NO_INIT_LADDERS)
            break
          case ERR_CODES.GET_LINK_NO_LADDERS:
            this.errMsg = ''
            this.$gaVisitor.event(
              GA_EVENTS.EVENT_NO_LADDERS_AVAILABLE,
              GA_EVENTS.EVENT_TYPE_EVT
            )
            window.zhuge.track(ZHUGE_EVENTS.EVENT_NO_LADDERS_AVAILABLE)
            break
          case ERR_CODES.GET_LINK_NEED_EXTRACT_CODE:
            this.errMsg = ''
            this.needExtractCode = true
            this.isExtractCodeCorrect = false
            this.$gaVisitor
              .event(GA_EVENTS.EVENT_GET_FILE_NEED_SHARECODE, GA_EVENTS.EVENT_TYPE_EVT)
            window.zhuge.track(ZHUGE_EVENTS.EVENT_GET_FILE_NEED_SHARECODE)
            break
          case ERR_CODES.GET_LINK_EXTRACT_CODE_WRONG:
            this.errMsg = ''
            this.needExtractCode = true
            this.isExtractCodeCorrect = false
            this.$gaVisitor
              .event(GA_EVENTS.EVENT_GET_FILE_NEED_SHARECODE, GA_EVENTS.EVENT_TYPE_EVT)
            window.zhuge.track(ZHUGE_EVENTS.EVENT_GET_FILE_NEED_SHARECODE)
            if (this.extractVerCode.length === 0) {
              this.getCaptcha()
            }
            break
          case ERR_CODES.GET_LINK_EXTRACT_CAPTCHA_WRONG:
            this.errMsg = ''
            this.extractNeedVercode = true
            this.$gaVisitor
              .event(GA_EVENTS.EVENT_GET_FILE_NEED_VERCODE, GA_EVENTS.EVENT_TYPE_EVT)
            window.zhuge.track(ZHUGE_EVENTS.EVENT_GET_FILE_NEED_VERCODE)
            if (this.extractVerCode.length === 0) {
              this.getCaptcha()
            }
            break
          case ERR_CODES.GET_LINK_EXTRACT_FAILED:
            this.errMsg = ''
            this.$gaVisitor
              .event(
                GA_EVENTS.EVENT_GET_FILE_FAILED,
                GA_EVENTS.EVENT_TYPE_EVT,
                btoa(
                  `url=${encodeURIComponent(this.shareLink)};sharecode=${
                    this.extractCode
                  };errno=${err.baiduErrno};errObj=${JSON.stringify(err.errObj)}`
                )
              )
            window.zhuge.track(ZHUGE_EVENTS.EVENT_GET_FILE_FAILED, {
              link: this.shareLink,
              sharecode: this.extractCode,
              errno: err.baiduErrno,
              errObj: JSON.stringify(err.errObj),
            })
            break
          case ERR_CODES.GET_LINK_SHARE_URL_BANNED:
            this.errMsg = err.msg || ''
            this.$gaVisitor
              .event(
                GA_EVENTS.EVENT_GET_FILE_SHARE_URL_BANNED,
                GA_EVENTS.EVENT_TYPE_EVT,
                btoa(
                  `url=${encodeURIComponent(this.shareLink)};sharecode=${
                    this.extractCode
                  };`
                )
              )
            window.zhuge.track(ZHUGE_EVENTS.EVENT_GET_FILE_SHARE_URL_BANNED, {
              link: this.shareLink,
              sharecode: this.extractCode,
            })
            reportResourceValidity(this.shareLink, false)
            break
          case undefined:
            this.errMsg = ''
            break
          default:
            this.errMsg = ''
            this.$gaVisitor
              .event(
                GA_EVENTS.EVENT_GET_FILE_FAILED,
                GA_EVENTS.EVENT_TYPE_EVT,
                btoa(
                  `url=${encodeURIComponent(this.shareLink)};sharecode=${
                    this.extractCode
                  };errno=${err.baiduErrno};errObj=${JSON.stringify(err)}`
                )
              )
            window.zhuge.track(ZHUGE_EVENTS.EVENT_GET_FILE_FAILED, {
              link: this.shareLink,
              sharecode: this.extractCode,
              errno: err.baiduErrno,
              errObj: JSON.stringify(err.errObj),
            })
            break
        }
      })
  }

  showBtFiles (data: any) {
    console.log('showing bt files')
    this.fileList = btFileConverter(data)
    postMessage(MESSAGES.FINISH_GETTING_BT_FILES)
    if (window.context === 'android') {
      return
    }
    if (this.isSingleFile) {
      this.checkedFiles = [this.fileList[0]]
      this.showFileSelector = true
      postMessage(MESSAGES.TREE_CHECKED, {
        taskType: this.taskType,
        files: this.checkedFiles,
      })
      return true
    } else {
      console.log('showing file selector')
      this.showFileSelector = true
      this.$nextTick(() => {
        console.log(this.fileList)
        const fileSelector = this.$refs['file-selector'] as FileSelector
        console.log(fileSelector)
        if (fileSelector) {
          fileSelector.genTreeData()
        }
      })
      return true
    }
  }

  onMagnetResult (data: any) {
    console.log('magnet uri parse result')
    this.taskType = 1
    if (typeof data === 'string') {
      this.errMsg = data
    } else if (data === null) {
      this.errMsg = this.parsingMagnetUri ? '' : 'BT'
    } else {
      this.BTParseFinished = true
      this.showBtFiles(data)
    }
    this.parsingMagnetUri = false
  }

  handleMagnetUri () {
    console.log('parsing magnet uri')
    console.log(this.inputLink)
    this.taskType = 1
    this.parsingMagnetUri = true
    this.parseMagnetTimeout = false
    postMessage(MESSAGES.SEND_MAGNET_URI, this.inputLink)
  }

  cancelParseMagnet () {
    postMessage(MESSAGES.CANCEL_PARSE_MAGNET_URI, this.inputLink)
    this.parsingMagnetUri = false
  }

  createOffline () {
    this.$emit('create-offline', this.inputLink)
  }

  getCaptcha () {
    if (this.fileGetter) {
      console.log('get captcha')
      this.fileGetter
        .getCaptcha()
        .then((res: any) => {
          this.extractVerCodeSrc = res.img
          this.verCodeStr = res.str
          return true
        })
        .catch(() => {
          this.errMsg = ''
        })
    }
  }

  onCheck (checkedFiles: FileItem[]) {
    console.log('tree check fired')
    this.checkedFiles = checkedFiles
    if (this.taskType === 0) {
      if (this.checkedFilesCount > MAX_DL_FILES) {
        this.errMsg = `${MAX_DL_FILES}`
      } else if (this.errMsg.indexOf('') > -1) {
        this.errMsg = ''
      }
      postMessage(MESSAGES.TREE_CHECKED, {
        taskType: this.taskType,
        files: this.checkedFiles,
        fileLimit: MAX_DL_FILES,
        overLimit: this.checkedFilesCount > MAX_DL_FILES,
      })
    }
    postMessage(MESSAGES.TREE_CHECKED, {
      taskType: this.taskType,
      files: this.checkedFiles,
    })
  }

  extractVerCodeEnterHndl () {
    if (this.fileList.length === 0) {
      this.parseLink()
    }
  }

  handleHTTPUrl () {
    console.log('parsing http link')
    console.log(this.inputLink)
    this.taskType = 2
    this.parsingHTTPUrl = true
    postMessage(MESSAGES.GET_HTTP_FILE_INFO, this.inputLink)
  }
  onHttpResult (data: any) {
    console.log('http url parse result')
    this.taskType = 2
    if (typeof data === 'string') {
      this.errMsg = data
    } else if (data === null) {
      this.errMsg = ''
    } else {
      this.showHTTPFile(data)
    }
    this.HTTPParseFinished = true
    this.parsingHTTPUrl = false
  }
  showHTTPFile (data: any) {
    console.log(data)
    console.log('showing http files')
    this.fileList = [{
      filename: data.filename || '',
      filetype: data.filetype,
      size: data.size,
      isDir: false,
      path: '',
    }]
    postMessage(MESSAGES.FINISH_GETTING_HTTP_FILE)
    if (window.context === 'android') {
      return
    }
    this.checkedFiles = [this.fileList[0]]
    this.showFileSelector = true
    postMessage(MESSAGES.TREE_CHECKED, {
      taskType: this.taskType,
      files: this.checkedFiles,
    })
    return true
  }
}
