import axios from 'axios'
import { DALIPAN_TRACKER_HOST } from '@/constants/constants'

const getClientType = () => {
  if (window.platform === 'mac' || window.platform === 'win' || /Electron/.test(navigator.userAgent)) {
    return 'enfi_desk'
  }
  if (window.platform === 'android' || /enfi-cloud-android/.test(navigator.userAgent) || /Android/.test(navigator.userAgent)) {
    return 'enfi_android'
  }
  return 'enfi_desk'
}

export const reportResourceValidity = (shareUrl: string, isValid: boolean, shareCode?: string) => {
  console.log('reporting validity to tracker')
  const reportData: { [key: string]: string | number } = {
    url: shareUrl,
    valid: isValid ? 1 : 0,
    client: getClientType(),
  }
  if (shareCode) {
    reportData.pwd = shareCode
  }
  axios({
    url: `${DALIPAN_TRACKER_HOST}/api/v2/report`,
    method: 'POST',
    headers: {
      apitoken: 'EwVy7wuVXIE7O15JIArhENFI',
    },
    data: [reportData],
  }).catch((err: any) => {
    console.error('report resource validity failed')
    console.error(err)
  })
}
