<template>
  <li
    class="enfi-flat-tree-item"
    :class="data[childrenKey] ? 'folder' : ''"
    v-if="data.toshow"
    :style="getItemStyle"
    @click="handleClick"
  >
    <span :class="arrowClasses" @click.stop="handleExpand">
      <Icon v-if="showArrow" type="ios-arrow-forward"></Icon>
      <Icon v-if="showLoading" type="ios-loading" class="ivu-load-loop"></Icon>
    </span>
    <Checkbox
      v-if="showCheckbox"
      :value="data.checked"
      :indeterminate="data.indeterminate"
      :disabled="data.disabled || data.disableCheckbox"
      @click.native.prevent.stop="handleCheck"
    >
    </Checkbox>
    <Render v-if="data.render" :render="data.render" :data="data" :node="node"></Render>
    <span v-else :class="titleClasses" @click="handleSelect">{{ data.title }}</span>
  </li>
</template>
<script>
import Render from './render'

const prefixCls = 'ivu-tree'

export default {
  name: 'FlatTreeNode',
  components: { Render },
  props: {
    data: {
      type: Object,
      default () {
        return {}
      },
    },
    childrenKey: {
      type: String,
      default: 'children',
    },
    nodeKey: {
      type: Number,
    },
    showCheckbox: {
      type: Boolean,
      default: false,
    },
  },
  data () {
    return {
      prefixCls: prefixCls,
    }
  },
  computed: {
    selectedCls () {
      return [
        {
          [`${prefixCls}-node-selected`]: this.data.selected,
        },
      ]
    },
    arrowClasses () {
      return [
        `${prefixCls}-arrow`,
        {
          [`${prefixCls}-arrow-disabled`]: this.data.disabled,
          [`${prefixCls}-arrow-open`]: this.data.expand,
        },
      ]
    },
    titleClasses () {
      return [
        `${prefixCls}-title`,
        {
          [`${prefixCls}-title-selected`]: this.data.selected,
        },
      ]
    },
    showArrow () {
      return (
        (this.data[this.childrenKey] && this.data[this.childrenKey].length) ||
        ('loading' in this.data && !this.data.loading)
      )
    },
    showLoading () {
      return 'loading' in this.data && this.data.loading
    },
    node () {
      //  nodeflatState node 
      return [
        this.$parent.flatState,
        this.$parent.flatState.find(item => item.nodeKey === this.data.nodeKey),
      ]
    },

    getItemStyle () {
      if (this.data.path && this.data.path.length > 0) {
        return `padding-left: ${this.data.path.length * 18}px`
      }
      return ''
    },
  },
  methods: {
    handleExpand () {
      const item = this.data
      if (item.disabled) return

      if (item[this.childrenKey] && item[this.childrenKey].length) {
        this.$set(this.data, 'expand', !this.data.expand)
        this.$emit('toggle-expand', {
          expand: this.data.expand,
          nodeKey: this.data.nodeKey,
        })
      }
    },
    handleSelect () {
      if (this.data.disabled) return
      if (this.showCheckbox) {
        this.handleCheck()
      } else {
        this.$emit('on-selected', this.data.nodeKey)
      }
    },
    handleCheck () {
      console.log('handle node check')
      console.log(this.data.disabled)
      if (this.data.disabled) return
      const changes = {
        checked: !this.data.checked && !this.data.indeterminate,
        nodeKey: this.data.nodeKey,
      }
      console.log(changes)
      this.$emit('on-check', changes)
    },
    handleClick () {
      if (this.data[this.childrenKey]) {
        this.handleExpand()
      }
    },
  },
}
</script>
<style lang="scss" scoped>
.enfi-flat-tree-item.folder {
  cursor: pointer;
}
</style>
