import axios, { AxiosResponse } from 'axios'
import CryptoJS from 'crypto-js'
import qs from 'qs'
import * as ERR_CODES from '@/constants/errCodes'
import { OFFLINE_LADDER_HOST, PARSE_LADDER_HOST } from '@/constants/constants'
import { getOfflineChecker } from '@/services/server'
import { versionCompareWithMethod } from '@/utils/versionCompare'

/**
 * Baidupan share link parse service
 */

interface GetLinkPostData {
  shareUrl: string
  shareCode: string
  paths: string[]
  verCode?: string
  verCodeStr?: string
  uid?: string
  token?: string
}

interface GetFileListPostData {
  shareUrl: string
  shareCode: string
  verCode?: string
  verCodeStr?: string
  uid?: string
  token?: string
}

interface CreateOfflinePostData {
  magnetUri: string
  name?: string
}

const decryptKey = 'zTckeF$U#iMaufRo'

const decrypt = (encrypted: string) => {
  const decryptKeyUtf8 = CryptoJS.enc.Utf8.parse(decryptKey)
  const decrypted: any = CryptoJS.AES.decrypt(encrypted, decryptKeyUtf8, {
    iv: decryptKeyUtf8,
    mode: CryptoJS.mode.CBC,
    padding: CryptoJS.pad.Pkcs7,
  })
  return JSON.parse(decrypted.toString(CryptoJS.enc.Utf8))
}

export const getFileList: (payloads: GetFileListPostData) => Promise<any> = payloads => {
  console.log('getting baidu files from ladder')
  let apiVer = 'v1'
  if (versionCompareWithMethod('2.3.0', window.appver, 'lte')) {
    apiVer = 'v3'
    if (window.ladderApiVer === 'v2') {
      apiVer = 'v1'
    }
  }
  return axios({
    url: `${PARSE_LADDER_HOST}/${apiVer}/list?plt=${window.platform}&appv=${window.appver}&ref=bdp&enfi-uid=${payloads.uid}&enfi-token=${payloads.token}`,
    method: 'POST',
    data: qs.stringify({
      shareurl: payloads.shareUrl,
      sharecode: payloads.shareCode,
      vcode: payloads.verCode,
      vcode_str: payloads.verCodeStr,
    }),
    withCredentials: true,
  })
    .then((res: AxiosResponse<any>) => {
      // return Promise.reject({
      //   code: ERR_CODES.LADDER_SERVER_BUSY,
      // })
      if (res.data.code === ERR_CODES.SERVER_SUCCESS) {
        const resData = decrypt(res.data.data)
        return resData
      }
      return Promise.reject(res.data)
    })
    .catch((err: any) => {
      console.error('get file list failed from ladder')
      console.error(err)
      // if (
      //   err.code === ERR_CODES.LADDER_SERVER_BUSY ||
      //   err.code === ERR_CODES.LADDER_SERVER_ERROR ||
      //   err.code === ERR_CODES.LADDER_URL_ERROR ||
      //   err.code === undefined
      // ) {
      //   return Promise.reject(err)
      // }
      return Promise.reject(err)
    })
}

export const createOfflineTask = (payloads: CreateOfflinePostData) => {
  console.log('creating offline task from ladders')
  console.log(payloads)

  return getOfflineChecker()
    .then((checker: string) => {
      return axios({
        url: `${OFFLINE_LADDER_HOST}/v1/offline/task?plt=${window.platform}&appv=${window.appver}&ref=bdp&enfi-uid=${window.enfiUid}&enfi-token=${window.enfiToken}`,
        method: 'POST',
        data: {
          check: checker,
          url: payloads.magnetUri,
          name: payloads.name,
        },
        withCredentials: true,
      })
    })
    .then((res: AxiosResponse<any>): any => {
      // return Promise.reject({
      //   code: ERR_CODES.LADDER_SERVER_BUSY,
      // })
      if (res.data.code === ERR_CODES.SERVER_SUCCESS) {
        return true
      }
      return Promise.reject(res.data)
    })
    .catch(
      (err: any): any => {
        console.error('create offline task failed from ladder')
        console.error(err)
        return Promise.reject(err)
      },
    )
}
